"use client";
import { motion } from "framer-motion";
import { Award } from "lucide-react";

const certifications = [
  {
    name: "Cyber Threat Management",
    issuer: "Cisco",
    date: "Issued Nov 2025",
  },
  {
    name: "Learning Data Analytics: 1 Foundations",
    issuer: "LinkedIn",
    date: "Issued June 2025",
  },
  {
    name: "Relational Database",
    issuer: "freeCodeCamp",
    date: "Issued Feb 2025",
  },
  {
    name: "JavaScript Essentials 1",
    issuer: "Cisco",
    date: "Issued Oct 2024",
  },
  {
    name: "Responsive Web Design",
    issuer: "freeCodeCamp",
    date: "Issued Sep 2024",
  },
  {
    name: "CompTIA IT Fundamentals+ (ITF+) Certification",
    issuer: "CompTIA",
    date: "Issued Nov 2023",
  },
];

export const Certifications = () => {
  return (
    <motion.div
      id="certifications"
      className="flex flex-col py-20 md:py-30 items-center pt-30"
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      viewport={{ once: true, amount: 0.3 }}
      transition={{ duration: 1 }}
    >
      <div className="flex flex-col items-center justify-center px-4 sm:px-6 md:px-10 text-center">
        <h1 className="text-4xl sm:text-5xl font-bold mb-6 sm:mb-8">
          Certifications
        </h1>
        <p className="text-base sm:text-lg mb-12 max-w-full sm:max-w-xl">
          A selection of my certifications demonstrating my skills and professional achievements.
        </p>
      </div>

      <div className="flex flex-col items-center justify-center pt-0 text-center w-full">
      <div className="grid grid-cols-1 sm:grid-cols-3 gap-y-6 gap-x-6 w-full">
        {certifications.map((cert, index) => (
          <div
            key={index}
            className="flex items-start gap-4 w-full p-4"
          >
            <div className="p-1 shrink-0">
              <Award className="h-6 w-6 text-[#1da1f2]" />
            </div>
            <div className="text-left">
              <h3 className="font-semibold text-lg sm:text-xl">{cert.name}</h3>
              <p className="text-sm sm:text-base">{cert.issuer}</p>
              <p className="text-xs sm:text-sm">{cert.date}</p>
            </div>
          </div>
        ))}
      </div>
    </div>
    </motion.div>
  );
};
